import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
// import _404 from './404';
// import Placeholder from '../components/Placeholder';
import Dashboard from '../pages/Dashboard';
import { Query } from "react-apollo";
import { LOCALLOGIN } from '../graphql/query/local';

class Index extends Component {
    render() {
        console.log("assadfas-f---------------asdfasdf");
        return (
            <Query query={LOCALLOGIN}>
                {
                    ({client, loading, error, data })=> {
                        console.log("ROOT Query login", data);
                        if(data.localLogin && data.localLogin.token){
                            return (
                                <Dashboard  {...this.props} client={client}></Dashboard>
                            );
                        }
                        else{
                            return (<Redirect to="/login"></Redirect>);
                        }
                    }
                }
            </Query>
        );
    }
}

export default Index;



