import { gql } from 'apollo-boost';

export const LOCALLOGIN = gql`
    mutation localLogin($token: String!){
        localLogin(token: $token)@client{
            token
        }
    }
`;

export const SITEENTRYTABLE = gql`
    mutation siteEntryTable(
        $order: String,
        $orderBy: String,
        $selected: [String],
        $page: Int,
        $rowsPerPage: Int
    )
    {
        siteEntryTable(
            order: $order,
            orderBy: $orderBy,
            selected: $selected,
            page: $page,
            rowsPerPage: $rowsPerPage) @client {
                order,
                orderBy,
                selected,
                page,
                rowsPerPage
            }
    }
`;

export const USERENTRYTABLE = gql`
    mutation userEntryTable(
        $order: String,
        $orderBy: String,
        $selected: [String],
        $page: Int,
        $rowsPerPage: Int
    )
    {
        userEntryTable(
            order: $order,
            orderBy: $orderBy,
            selected: $selected,
            page: $page,
            rowsPerPage: $rowsPerPage) @client {
                order,
                orderBy,
                selected,
                page,
                rowsPerPage
            }
    }
`;



