import React from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import FilterListIcon from '@material-ui/icons/FilterList';
import { lighten } from '@material-ui/core/styles/colorManipulator';
import formatDate from '../utils/date';

function getSorting(order, orderBy) {
    return order === 'desc'
        ? (a, b) => (b[orderBy] < a[orderBy] ? -1 : 1)
        : (a, b) => (a[orderBy] < b[orderBy] ? -1 : 1);
}

function* getColor({ primary = "#35A7FF", secondary = "#FF5964" } = {}) {
    let color = primary;
    while (true) {
        color = color === secondary ? primary : secondary;
        yield color;
    }
}

class EnhancedTableHead extends React.Component {
    createSortHandler = property => event => {
        this.props.onRequestSort(event, property);
    };

    render() {
        const { onSelectAllClick, order, orderBy, numSelected, rowCount, columnData } = this.props;
        let color = getColor();
        let first = true;
        return (
            <TableHead>
                <TableRow>
                    <TableCell padding="checkbox">
                        <Checkbox
                            indeterminate={numSelected > 0 && numSelected < rowCount}
                            checked={numSelected === rowCount}
                            onChange={onSelectAllClick}
                        />
                    </TableCell>
                    {columnData.map(column => {
                        let colorValue = null;
                        if (first) {
                            colorValue = null;
                            first = false;
                        }
                        else {
                            colorValue = color.next().value;
                        }
                        return (
                            <TableCell
                                key={column.id}
                                numeric={column.numeric}
                                padding={column.disablePadding ? 'none' : 'default'}
                                sortDirection={orderBy === column.id ? order : false}
                                colSpan={column.colSpan}
                            >
                                <Tooltip
                                    title="Sort"
                                    placement={column.numeric ? 'bottom-end' : 'bottom-start'}
                                    enterDelay={300}
                                >
                                    <TableSortLabel
                                        active={orderBy === column.id}
                                        direction={order}
                                        onClick={this.createSortHandler(column.id)}
                                        style={{ color: colorValue }}
                                    >
                                        {column.label}
                                    </TableSortLabel>
                                </Tooltip>
                            </TableCell>
                        );
                    }, this)}
                </TableRow>
            </TableHead>
        );
    }
}

EnhancedTableHead.propTypes = {
    numSelected: PropTypes.number.isRequired,
    onRequestSort: PropTypes.func.isRequired,
    onSelectAllClick: PropTypes.func.isRequired,
    order: PropTypes.string.isRequired,
    orderBy: PropTypes.string.isRequired,
    rowCount: PropTypes.number.isRequired,
};

const toolbarStyles = theme => ({
    root: {
        paddingRight: theme.spacing.unit,
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    spacer: {
        flex: '1 1 50%',
    },
    actions: {
        color: theme.palette.text.secondary,
    },
    title: {
        flex: '0 0 auto',
    },
});

let EnhancedTableToolbar = props => {
    const { numSelected, selected, classes, title, editable, deletable, deleteMutate } = props;

    return (
        <Toolbar
            className={classNames(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >
            <div className={classes.title}>
                {numSelected > 0 ? (
                    <Typography color="inherit" variant="subheading">
                        {numSelected} selected
                        </Typography>
                ) : (
                        <Typography variant="title" id="tableTitle">
                            {title}
                        </Typography>
                    )}
            </div>
            <div className={classes.spacer} />
            <div className={classes.actions}>
                {numSelected > 0 ? (
                    numSelected === 1 ? (
                        <Grid container direction="row" justify="space-between">
                            {
                                editable ?
                                    <Grid item xs={6}>
                                        <Tooltip title="Edit">
                                            <IconButton aria-label="Edit" onClick={e => console.log("edited")}>
                                                <EditIcon />
                                            </IconButton>
                                        </Tooltip>
                                    </Grid> :
                                    null
                            }
                            {
                                deletable ?
                                    <Grid item xs={6}>
                                        <Tooltip title="Delete">
                                            <IconButton aria-label="Delete" onClick={e => deleteMutate({ids: selected})}>
                                                <DeleteIcon />
                                            </IconButton>
                                        </Tooltip>
                                    </Grid> :
                                    null
                            }
                        </Grid>
                    ) : (
                            <Tooltip title="Delete">
                                <IconButton aria-label="Delete" onClick={e => deleteMutate({ids: selected})}>
                                    <DeleteIcon />
                                </IconButton>
                            </Tooltip>
                        )
                ) : (
                        <Tooltip title="Filter list">
                            <IconButton aria-label="Filter list">
                                <FilterListIcon />
                            </IconButton>
                        </Tooltip>
                    )}
            </div>
        </Toolbar>
    );
};

EnhancedTableToolbar.propTypes = {
    classes: PropTypes.object.isRequired,
    numSelected: PropTypes.number.isRequired,
};

EnhancedTableToolbar = withStyles(toolbarStyles)(EnhancedTableToolbar);

const styles = theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing.unit * 3,
    },
    table: {
        minWidth: '100%',        
    },
    tableWrapper: {
        overflowX: 'auto',
    },
});

class EnhancedTable extends React.Component {
    componentWillMount() {
        this.props.refetch();
        // this.props.mutate(defaultState.siteEntryTable);
    }
    filter(obj, exclude) {
        return Object.keys(obj)
            .filter(key => !exclude.includes(key))
            .reduce((result, key) => {
                result[key] = obj[key];
                return result;
            }, {});
    }
    handleRequestSort = async (event, property) => {
        const orderBy = property;
        let order = 'asc';

        if (this.props.settings.orderBy === property && this.props.settings.order === 'asc') {
            order = 'desc';
        }
        await this.props.mutate({ ...this.props.settings, order, orderBy });
        // this.setprops({ order, orderBy });
    };

    handleSelectAllClick = async (event, checked) => {
        console.log("data.length...", this.props.data.length);
        if (checked) {
            await this.props.mutate({ ...this.props.settings, selected: this.props.data.map(n => n.id) });
            // this.setprops(props => ({ selected: props.data.map(n => n.id) }));
            return;
        }
        await this.props.mutate({ ...this.props.settings, selected: [] });
        // this.setprops({ selected: [] });
    };

    handleClick = async (event, id) => {
        const { selected } = this.props.settings;
        const selectedIndex = selected.indexOf(id);
        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, id);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                selected.slice(0, selectedIndex),
                selected.slice(selectedIndex + 1),
            );
        }
        await this.props.mutate({ ...this.props.settings, selected: newSelected });
        // this.setprops({ selected: newSelected });
    };

    handleChangePage = async (event, page) => {
        // this.setprops({ page });
        console.log("Page Change..", page);

        await this.props.mutate({ ...this.props.settings, page, selected: [] });
    };

    isSelected = id => this.props.settings.selected.indexOf(id) !== -1;

    render() {
        const { data, count, settings: { order, orderBy, selected = [], rowsPerPage, page }, title, columnData, exclude, onClick = e => null, editable, deletable, deleteMutate = e => null, classes } = this.props;
        if (data.length === 0) {
            return (
                <h1>No Entry Yet!</h1>
            );
        }
        console.log("settings...", this.props.settings);
        console.log("data", data);
        const renderData = [].concat(data)
            .sort(getSorting(order, orderBy));
        console.log("renderData...", renderData);
        return (
            <Paper className={classes.root}>
                <EnhancedTableToolbar numSelected={selected.length} selected={selected} editable={editable} deletable={deletable} deleteMutate={deleteMutate} title={title} />
                <div className={classes.tableWrapper}>
                    <Table className={classes.table} aria-labelledby="tableTitle">
                        <EnhancedTableHead
                            numSelected={selected.length}
                            order={order}
                            orderBy={orderBy}
                            onSelectAllClick={this.handleSelectAllClick}
                            onRequestSort={this.handleRequestSort}
                            rowCount={data.length}
                            columnData={columnData}
                        />
                        <TableBody>
                            {renderData.map(n => {
                                // console.log(n);
                                const isSelected = this.isSelected(n.id);
                                return (
                                    <TableRow
                                        hover
                                        role="checkbox"
                                        aria-checked={isSelected}
                                        tabIndex={-1}
                                        key={n.id}
                                        onClick={e => onClick(n)}
                                        selected={isSelected}
                                    >
                                        <TableCell padding="checkbox">
                                            <Checkbox checked={isSelected} onClick={event => this.handleClick(event, n.id)} />
                                        </TableCell>
                                        <TableCell component="th" scope="row" padding="none">
                                            {formatDate(n.createdAt)}
                                        </TableCell>
                                        {
                                            (() => {
                                                const rest = this.filter(n, exclude);
                                                let color = getColor();
                                                return Object.values(rest).map(e => {
                                                    let colorValue = color.next().value;
                                                    if (!(e instanceof Object)) {
                                                        return (
                                                            <TableCell style={{ color: colorValue }} key={Math.random()}>{e}</TableCell>
                                                        );
                                                    }
                                                    const { __typename, ...rest } = e;
                                                    return (
                                                        <React.Fragment key={Math.random()}>
                                                            {
                                                                Object.values(rest).map(e => (
                                                                    <TableCell style={{ color: colorValue }} key={Math.random()}>{e}</TableCell>
                                                                ))
                                                            }
                                                        </React.Fragment>
                                                    );
                                                })
                                            })()
                                        }
                                    </TableRow>
                                );
                            })}
                        </TableBody>
                    </Table>
                </div>
                <TablePagination
                    component="div"
                    count={count}
                    rowsPerPage={rowsPerPage}
                    rowsPerPageOptions={[15]}
                    page={page}
                    backIconButtonProps={{
                        'aria-label': 'Previous Page',
                    }}
                    nextIconButtonProps={{
                        'aria-label': 'Next Page',
                    }}
                    onChangePage={this.handleChangePage}
                />
            </Paper>
        );
    }
}

EnhancedTable.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(EnhancedTable);



