import React, { Component } from 'react';

import { Query } from "react-apollo";
import { LOCALLOGIN } from '../graphql/query/local';
import Header from './Header';
import Body from './Body';
import Footer from './Footer';

class Layout extends Component {
    render() {
        return (
            <Query query={LOCALLOGIN}>
                {
                    ({ client, loading, error, data }) => {
                        return (
                            <React.Fragment>
                                <Header {...this.props} client={client}></Header>
                                <Body>
                                    <div className="pv3">
                                        {this.props.children}
                                    </div>
                                </Body>
                                <Footer></Footer>
                            </React.Fragment>
                        );
                    }
                }
            </Query>
        );
    }
}

export default Layout;



